import React, { PureComponent } from 'react'

import './input-group.less'

class InputGroup extends PureComponent {
  static defaultProps = {
    count: 1,
    defaultValues: [],
    onChange: () => {}
  }

  constructor (props) {
    super(props)
    this.reg = /^[1-9]\d*$/
    this.state = {
      values: [...props.defaultValues]
    }
  }

  onInputChange = (index) => (e) => {
    const values = this.state.values
    const value = e.target.value
    if (value === '' || this.reg.test(value)) {
      values[index] = value ? Number(value) : value
      const newValues = [...values].filter(v => v !== '')
      this.props.onChange(newValues)
      this.setState({
        values: newValues
      })
    }
  }

  renderInput () {
    const { count } = this.props
    const { values } = this.state
    const doms = []
    for (let i = 0; i < count; i++) {
      doms.push(
        <input
          key={`input-${i}`}
          value={values[i] || ''}
          onChange={this.onInputChange(i)}/>
      )
    }
    return doms
  }

  render () {
    return (
      <div className="input-group">
        {this.renderInput()}
      </div>
    )
  }
}

export default InputGroup
