import React, { PureComponent } from 'react'
import { Scrollbars } from 'react-custom-scrollbars'
import Switch from '../../../component/switch/Switch'
import InputNumber from '../../../component/inputnumber/InputNumber'
import Select from '../../../component/select/Select'
import ColorPicker from '../../../component/colorpicker/ColorPicker'
import RadioGroup from '../../../component/radiogroup/RadioGroup'
import Button from '../../../component/button/Button'
import ExpandList from '../../../component/expandlist/ExpandList'
import InputGroup from '../../../component/inputgroup/InputGroup'

import {
  xAxisStyles, yAxisStyles, candleStickStyles, realTimeStyles, technicalIndicatorStyles,
  floatLayerStyles, gridStyles, graphicMarkStyles, separatorStyles, commonStyles, indicatorParams
} from './data'

class SettingList extends PureComponent {
  static defaultProps = {
    onStyleChange: () => {},
    onSpecialConfigChange: () => {},
    onIndicatorParamsChange: () => {}
  }

  state = {
    expand: {
      common: true,
      indicatorParams: false,
      marker: false,
      mainChart: false,
      xAxis: false,
      yAxis: false,
      indicator: false,
      tooltip: false,
      other: false
    }
  }

  /**
   * 
   * @param style
   * @returns {Function}
   */
  onValueChange = (style) => (value) => {
    if (style.special) {
      this.props.onSpecialConfigChange(style.special, value)
    } else {
      this.props.onStyleChange(style.key, value)
    }
  }

  /**
   * 
   * @param style
   * @returns {function(...[*]=)}
   */
  onInputGroupChange = (style) => (values) => {
    this.props.onIndicatorParamsChange(style.key, values)
  }

  /**
   * 
   * @param style
   * @returns {null|*}
   */
  getComponent (style) {
    switch (style.componentType) {
      case 'switch': {
        return (
          <Switch
            defaultValue={style.defaultValue}
            onSwitchChange={this.onValueChange(style)}/>
        )
      }
      case 'input': {
        return (
          <InputNumber
            defaultValue={style.defaultValue}
            onNumberChange={this.onValueChange(style)}/>
        )
      }
      case 'select': {
        return (
          <Select
            dataSource={style.dataSource || []}
            defaultValue={style.defaultValue}
            onSelectChange={this.onValueChange(style)}/>
        )
      }
      case 'color': {
        return (
          <ColorPicker
            defaultValue={style.defaultValue}
            onColorChange={this.onValueChange(style)}/>
        )
      }
      case 'radio': {
        return (
          <RadioGroup
            defaultValue={style.defaultValue}
            dataSource={style.dataSource}
            onRadioChange={this.onValueChange(style)}/>
        )
      }
      case 'button': {
        return (
          <Button
            text={style.buttonText}
            onClick={this.onValueChange(style)}/>
        )
      }
      case 'expandList': {
        return (
          <ExpandList
            dataSource={style.dataSource}
            onItemClick={this.onValueChange(style)}/>
        )
      }
      case 'inputGroup': {
        return (
          <InputGroup
            onChange={this.onInputGroupChange(style)}
            defaultValues={style.defaultValue}
            count={style.inputCount}/>
        )
      }
    }
  }

  onListTitleClick = (type) => () => {
    const expand = this.state.expand
    expand[type] = !expand[type]
    this.setState({ expand: { ...expand } })
  }

  /**
   * 
   */
  renderStyleSettingList (type) {
    const { expand } = this.state
    let styls = []
    let title = ''
    switch (type) {
      case 'common': {
        styls = commonStyles
        title = ''
        break
      }
      case 'technicalIndicatorParams': {
        styls = indicatorParams
        title = ''
        break
      }
      case 'graphicMark': {
        styls = graphicMarkStyles
        title = ''
        break
      }
      case 'candleStick': {
        styls = candleStickStyles
        title = ''
        break
      }
      case 'realTime': {
        styls = realTimeStyles
        title = ''
        break
      }
      case 'technicalIndicator': {
        styls = technicalIndicatorStyles
        title = ''
        break
      }
      case 'grid': {
        styls = gridStyles
        title = ''
        break
      }
      case 'separator': {
        styls = separatorStyles
        title = ''
        break
      }
      case 'xAxis': {
        styls = xAxisStyles
        title = ''
        break
      }
      case 'yAxis': {
        styls = yAxisStyles
        title = ''
        break
      }
      case 'floatLayer': {
        styls = floatLayerStyles
        title = ''
        break
      }
    }
    const isExpand = expand[type]
    const titleDom = (
      <div
        key={`${type}-title`}
        onClick={this.onListTitleClick(type)}
        className={`kline-setting-list-title ${isExpand && 'kline-setting-list-title-expand'}`}>
        <span className="kline-setting-list-title-text">{title}</span>
        <i className={`icon-arrow kline-setting-list-title-icon ${isExpand && 'kline-setting-list-title-icon-expand'}`}/>
      </div>
    )
    const styleDomList =
      <ul
        key={`${type}-list`}
        className={`kline-setting-list ${isExpand && 'kline-setting-list-expand'}`}>
        {
          styls.map((style) => {
            return (
              <li
                datatype={style.sub ? 'sub' : ''}
                key={style.key || style.special}
                className="kline-setting-list-row">
                { style.sub && (<i className="kline-setting-list-row-arrow"/>)}
                <span>{style.text}</span>
                {this.getComponent(style)}
              </li>
            )
          })
        }
      </ul>
    return [titleDom, styleDomList]
  }

  render () {
    return (
      <Scrollbars
        className="kline-setting-container">
        <div>
          { this.renderStyleSettingList('common') }
          { this.renderStyleSettingList('technicalIndicatorParams') }
          { this.renderStyleSettingList('graphicMark') }
          { this.renderStyleSettingList('candleStick') }
          { this.renderStyleSettingList('realTime') }
          { this.renderStyleSettingList('technicalIndicator') }
          { this.renderStyleSettingList('grid') }
          { this.renderStyleSettingList('separator') }
          { this.renderStyleSettingList('xAxis') }
          { this.renderStyleSettingList('yAxis') }
          { this.renderStyleSettingList('floatLayer') }
        </div>
      </Scrollbars>
    )
  }
}

export default SettingList
