import React, { PureComponent } from 'react'
import { Helmet } from 'react-helmet'
import { init, version, dispose } from 'klinecharts'
import FileSaver from 'file-saver'
import SettingList from './settinglist/SettingList'

import set from 'lodash/set'

import './kline.less'

class KLine extends PureComponent {
  constructor (props) {
    super(props)
    this.throttle = (func, wait) => {
      let previous = 0
      return () => {
        const now = Date.now()
        if (now - previous > wait) {
          func()
          previous = now
        }
      }
    }
    this.onResize = this.throttle(() => {
      if (this.chart) {
        this.chart.resize()
      }
    }, 1000 / 16)
    this.chart = null
    this.state = {
      isLoading: false
    }
  }

  componentDidMount () {
    this.chart = init('k_chart')
    this.chart.addTechnicalIndicator('VOL')
    this.chart.addTechnicalIndicator()
    const defaultDataList = this.generatedKLineDataList()
    this.chart.applyNewData(defaultDataList)
    this.chart.loadMore(() => {
      this.setState({ isLoading: true })
      setTimeout(() => {
        const dataList = this.chart.getDataList()
        this.setState({ isLoading: false })
        this.chart.applyMoreData(this.generatedKLineDataList(200, dataList[0].close, dataList[0].timestamp), true)
      }, 2000)
    })
    this.addData()
    window.addEventListener('resize', this.onResize, false)
  }

  addData () {
    setTimeout(() => {
      const dataList = this.chart.getDataList()
      const lastData = dataList[dataList.length - 1]
      const newData = { ...lastData }
      const nowTimestamp = Math.floor((new Date().getTime()) / 60 / 1000) * 60 * 1000
      if (new Date(nowTimestamp).getMinutes() > new Date(newData.timestamp).getMinutes()) {
        newData.high = newData.close
        newData.low = newData.close
        newData.open = newData.close
        newData.volume = 0
        newData.turnover = 0
        this.count = 1
      } else {
        const price = (Math.random() - 0.5) * 10 + newData.close
        const volume = +((Math.random() * 3).toFixed(0))
        if (this.count === 1) {
          newData.open = price
          newData.high = price
          newData.low = price
        }
        newData.close = price
        newData.high = Math.max(newData.high, price)
        newData.low = Math.min(newData.low, price)
        newData.volume = newData.volume + volume
        newData.turnover = newData.turnover + volume * price
        this.count++
      }
      newData.timestamp = nowTimestamp
      this.chart.updateData(newData)
      this.addData()
    }, 1000)
  }

  componentWillUnmount () {
    dispose(this.chart)
    window.removeEventListener('resize', this.onResize, false)
  }

  /**
   * k
   * @returns {Array}
   */
  generatedKLineDataList (dataSize = 500, basePrice = Math.random() * 12000, nowTimestamp = new Date().getTime()) {
    const dataList = []
    let timestamp = Math.floor(nowTimestamp / 1000 / 60) * 1000 * 60
    let baseValue = basePrice
    const prices = []
    for (let i = 0; i < dataSize; i++) {
      baseValue = baseValue + Math.random() * 20 - 10
      for (let j = 0; j < 4; j++) {
        prices[j] = (Math.random() - 0.5) * 12 + baseValue
      }
      prices.sort()
      const openIdx = +Math.round(Math.random() * 3).toFixed(0)
      let closeIdx = +Math.round(Math.random() * 2).toFixed(0)
      if (closeIdx === openIdx) {
        closeIdx++
      }
      const volume = Math.random() * 200 + 10
      timestamp -= 60 * 1000
      const kLineModel = {
        open: prices[openIdx],
        low: prices[0],
        high: prices[3],
        close: prices[closeIdx],
        volume: volume,
        timestamp
      }
      kLineModel.turnover = (kLineModel.open + kLineModel.close + kLineModel.high + kLineModel.low) / 4 * volume
      if (basePrice) {
        dataList.unshift(kLineModel)
      } else {
        dataList.push(kLineModel)
      }
    }
    return dataList
  }

  /**
   * 
   * @param key
   * @param value
   */
  onStyleChange = (key, value) => {
    if (this.chart) {
      const c = set(this.chart.getStyleOptions(), key, value)
      this.chart.setStyleOptions(c)
    }
  }

  /**
   * 
   * @param specialType
   * @param value
   */
  onSpecialConfigChange = (specialType, value) => {
    switch (specialType) {
      case 'export': {
        const file = new File([JSON.stringify(this.chart.getStyleOptions())], 'style.json', { type: 'text/plain;charset=utf-8' })
        FileSaver.saveAs(file)
        break
      }
      case 'picture': {
        const url = this.chart.getConvertPictureUrl(true, true)
        const a = document.createElement('a')
        a.download = 'chart'
        a.href = url
        document.body.appendChild(a)
        a.click()
        a.remove()
        break
      }
      case 'dataSpace': {
        this.chart.setDataSpace(value)
        break
      }
      case 'offsetRightSpace': {
        this.chart.setOffsetRightSpace(value)
        break
      }
      case 'candleStickTechnicalIndicator': {
        this.chart.setCandleStickTechnicalIndicatorType(value)
        break
      }
      case 'addTechnicalIndicator': {
        this.chart.addTechnicalIndicator(value)
        break
      }
      case 'addGraphicMark': {
        this.chart.addGraphicMark(value.key)
        break
      }
      case 'removeAllGraphicMark': {
        this.chart.removeAllGraphicMark()
        break
      }
      case 'chartType': {
        this.chart.setCandleStickChartType(value)
        break
      }
    }
  }

  onIndicatorParamsChange = (indicatorType, values) => {
    this.chart.applyTechnicalIndicatorParams(indicatorType, values)
  }

  render () {
    return (
      <div className="kline">
        <Helmet>
          <title>k(K Stock Chart)</title>
          <meta name="keywords" content="kline,stocks,candlestick,time-line,tradingview,canvas,charts"/>
          <meta name="description" content="kdemo(K stock chart demo)" />
        </Helmet>
        <div className="kline-header">
          <span>
            K
            <span className="kline-header-version">&nbsp;(v{version()})</span>
          </span>
          <a
            target="_blank"
            rel="noopener noreferrer nofollow"
            href="https://github.com/liihuu/klineweb"
            className="icon-github kline-header-link-icon"/>
        </div>
        <div className="kline-content-container">
          <SettingList
            onStyleChange={this.onStyleChange}
            onSpecialConfigChange={this.onSpecialConfigChange}
            onIndicatorParamsChange={this.onIndicatorParamsChange}/>
          <div className="kline-chart-container">
            <div
              id="k_chart"
              className="kline-chart"/>
            {
              this.state.isLoading &&
              <div className="kline-chart-loading-container">
                <i className="kline-chart-loading"/>
                <span className="kline-chart-loading-text"></span>
              </div>
            }
          </div>
        </div>
      </div>
    )
  }
}

export default KLine
