import React, { PureComponent } from 'react'
import RCColorPicker from 'rc-color-picker'
import 'rc-color-picker/assets/index.css'
import './color-picker.less'

const colorReg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/

class ColorPicker extends PureComponent {
  static defaultProps = {
    defaultValue: '#ffffff',
    onColorChange: () => {}
  }

  constructor (props) {
    super(props)
    this.state = {
      color: props.defaultValue
    }
  }

  /**
   * 16rgb
   * @param colorData
   * @returns {string}
   */
  colorRgb (colorData) {
    let color = colorData.color.toLowerCase()
    const alpha = colorData.alpha / 100
    if (color && colorReg.test(color) && alpha < 100) {
      if (color.length === 4) {
        let colorNew = '#'
        for (let i = 1; i < 4; i += 1) {
          colorNew += color.slice(i, i + 1).concat(color.slice(i, i + 1))
        }
        color = colorNew
      }
      //  
      const colorChange = []
      for (let i = 1; i < 7; i += 2) {
        colorChange.push(parseInt('0x' + color.slice(i, i + 2)))
      }
      return `rgba(${colorChange.join(',')},${alpha})`
    } else {
      return color
    }
  }

  colorPickerChange = (data) => {
    const color = this.colorRgb(data)
    this.props.onColorChange(color)
    this.setState({ color })
  }

  render () {
    return (
      <div className="color-picker">
        <span className="color-picker-value">{this.state.color}</span>
        <RCColorPicker
          defaultColor={this.props.defaultValue}
          defaultAlpha={100}
          onChange={this.colorPickerChange}
        />
      </div>
    )
  }
}

export default ColorPicker
