import React, { PureComponent } from 'react'

import './expand-list.less'

class ExpandList extends PureComponent {
  static defaultProps = {
    dataSource: [],
    onItemClick: () => {}
  }

  render () {
    return (
      <div className="expand-list">
        <i className="icon-draw expand-list-icon"/>
        <div className="expand-list-content">
          <i className="expand-list-arrow"/>
          <ul
            className="expand-list-container">
            {
              this.props.dataSource.map(data => {
                return (
                  <li
                    key={data.key}
                    title={data.title}
                    onClick={() => { this.props.onItemClick(data) }}>
                    <span className="expand-list-icon-container">
                      {data.text}
                    </span>
                  </li>
                )
              })
            }
          </ul>
        </div>
      </div>
    )
  }
}

export default ExpandList
