import React, { PureComponent } from 'react'
import './button.less'

class Button extends PureComponent {
  static defaultProps = {
    text: '',
    onClick: () => {}
  }

  render () {
    const { text, onClick } = this.props
    return (
      <button className="button" onClick={onClick}>{text}</button>
    )
  }
}

export default Button
