import React, { PureComponent } from 'react'
import './radio-group.less'

class RadioGroup extends PureComponent {
  static defaultProps = {
    dataSource: [],
    defaultValue: {},
    onRadioChange: () => {}
  }

  constructor (props) {
    super(props)
    this.state = {
      value: props.defaultValue
    }
  }

  onRadioClick = value => e => {
    e.stopPropagation()
    this.props.onRadioChange(value.key)
    this.setState({ value })
  }

  render () {
    return (
      <div className="radio-group">
        {
          this.props.dataSource.map(data => {
            const isChecked = data.key === this.state.value.key
            return (
              <div
                key={data.key}
                className={`radio-container ${isChecked ? 'radio-container-check' : ''}`}
                onClick={this.onRadioClick(data)}>
                <i className="radio"/>
                <span>{data.text}</span>
              </div>
            )
          })
        }
      </div>
    )
  }
}

export default RadioGroup
